import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import signature from './../images/signature.jpg'
import video from './../images/office.jpg';
import play from './../images/play-solid.svg';
import { positionFooter } from './../index';

const MainPage = (props) => {
  useEffect(() => {
    positionFooter();
  })

  return (
    <div className="mainpage">
      <div className="about">
        <h2 className="title">About Us</h2>
        <p className="description">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
        <div className="signature">
          <img alt="signature" src={signature} />
        </div>
        <div className="workprocess">
          <h2 className="title">Our Work Process</h2>
          <p className="description">Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum.</p>
          <div className="video col-9">
            <img src={video} alt="video" />
            <div className="playButton">
              <div className="inner"><img src={play} alt="play" /></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

const MainPageContainer = (props) => {
  return (
    <MainPage {...props} />
  )
}

let mapStateToProps = (state) => ({
})

let mapDispatchToProps = (dispatch) => ({

})

export default connect(mapStateToProps, mapDispatchToProps)(MainPageContainer);