const CHANGE_NAME = "CHANGE_NAME";
const CHANGE_LAST_NAME = "CHANGE_LAST_NAME";
const CHANGE_EMAIL = "CHANGE_EMAIL";
const CHANGE_HTML_LEVEL = "CHANGE_HTML_LEVEL";
const CHANGE_CSS_LEVEL = "CHANGE_CSS_LEVEL";
const CHANGE_JS_LEVEL = "CHANGE_JS_LEVEL";

let initialState = {
  name: "",
  lastName: "",
  email: "",
  htmlLevel: 0,
  cssLevel: 0,
  jsLevel: 0
}

const CVPageReducer = (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_NAME:
      return {
        ...state,
        name: action.name
      }
    case CHANGE_LAST_NAME:
      return {
        ...state,
        lastName: action.lastName
      }
    case CHANGE_EMAIL:
      return {
        ...state,
        email: action.email
      }
    case CHANGE_HTML_LEVEL:
      return {
        ...state,
        htmlLevel: action.htmlLevel
      }
    case CHANGE_CSS_LEVEL:
      return {
        ...state,
        cssLevel: action.cssLevel
      }
    case CHANGE_JS_LEVEL:
      return {
        ...state,
        jsLevel: action.jsLevel
      }
    default:
      return state;
  }
}

export const setNameActionCreator = (name) => ({ type: CHANGE_NAME, name });
export const setLastNameActionCreator = (lastName) => ({ type: CHANGE_LAST_NAME, lastName });
export const setEmailActionCreator = (email) => ({ type: CHANGE_EMAIL, email });
export const setHTMLActionCreator = (htmlLevel) => ({ type: CHANGE_HTML_LEVEL, htmlLevel });
export const setCSSActionCreator = (cssLevel) => ({ type: CHANGE_CSS_LEVEL, cssLevel });
export const setJSActionCreator = (jsLevel) => ({ type: CHANGE_JS_LEVEL, jsLevel });

export default CVPageReducer;