import React, { useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import bars from './../images/bars-solid.svg'

const Header = (props) => {
  useEffect(() => {
    let barsbtn = document.getElementById("bars");
    let dropMenu = document.querySelector(".dropmenu");
    barsbtn.addEventListener('click', () => {
      dropMenu.classList.toggle("active");
      barsbtn.classList.toggle('rotated');
    })
  }, [])

  return (
    <>
      <header className="container">
        <div className="row">
          <div className="title col-3">
            <h1 id="nameOfSite">Simer</h1>
          </div>
          <div className="navigation col-9">
            <nav>
              <ul className="menu">
                <li><NavLink to="/mainpage">Main</NavLink></li>
                <li><NavLink to="/services">Services</NavLink></li>
                <li><NavLink to="/makeCV">Make a CV</NavLink></li>
                <li id="bars">
                  <img src={bars} alt="bars" />
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </header>
      <ul className="dropmenu">
        <li><NavLink to="/mainpage">Main</NavLink></li>
        <li><NavLink to="/services">Services</NavLink></li>
        <li><NavLink to="/makeCV">Make a CV</NavLink></li>
      </ul>
    </>
  )
}

export default Header;