import './App.css';
import React from 'react';
import { compose } from 'redux';
import {
  // BrowserRouter,
  withRouter,
  Route,
  Switch,
  Redirect,
  HashRouter
} from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './Redux/redux-store';
import ServicesContainer from './Components/Services';
import Header from './Components/Header';
import CVContainer from './Components/CV';
import MainPageContainer from './Components/MainPage';
import Footer from './Components/Footer';

class App extends React.Component {
  render() {
    return (
      <div>
        <div className="header">
          <Header />
        </div>
        <div className="content container">
          <Switch>
          <Route exact path='/' render={() => <Redirect to="/mainpage" />} />
            <Route path='/mainpage' render={() => <MainPageContainer />} />
            <Route path='/services' render={() => <ServicesContainer />} />
            <Route path='/makeCV' render={() => <CVContainer />} />
          </Switch>
        </div>
        <div className="footer">sd
          <Footer />
        </div>
      </div>
    )
  }
}


let AppContainer = compose(
  withRouter
)(App);

const AppComponent = (props) => {
  return (
    // <BrowserRouter>
    <HashRouter>
      <Provider store={store}>
        <AppContainer {...props} name="Hrant" />
      </Provider>
    </HashRouter>
    /* </BrowserRouter> */
  )
}

export default AppComponent;
