import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import {
  setNameActionCreator,
  setLastNameActionCreator,
  setEmailActionCreator,
  setHTMLActionCreator,
  setCSSActionCreator,
  setJSActionCreator,
} from '../Redux/Reducers/CVPageReducer';
import { positionFooter } from './../index';

const CVForm = (props) => {
  const onChange = (e, type) => {
    switch (type) {
      case "name":
        if (e.target.value.length <= 20)
          props.setName(e.target.value)
        break;
      case "lastName":
        if (e.target.value.length <= 20)
          props.setLastName(e.target.value)
        break;
      case "email":
        if (e.target.value.length <= 30)
          props.setEmail(e.target.value)
        break;
      case "html":
        if (+e.target.value <= 100)
          props.setHTML(+e.target.value)
        break;
      case "css":
        if (+e.target.value <= 100)
          props.setCSS(+e.target.value)
        break;
      case "js":
        if (+e.target.value <= 100)
          props.setJS(+e.target.value)
        break;
      default:
        return
    }
  }

  const onSubmit = (e, json) => {
    e.preventDefault();
    alert(json);
  }

  return (
    <form onSubmit={(e) => {
      onSubmit(e, JSON.stringify({
        name: props.name,
        lastName: props.lastName,
        email: props.email,
        htmlLevel: props.htmlLevel,
        cssLevel: props.cssLevel,
        jsLevel: props.jsLevel
      }, null, 2))
    }}>
      <div className="about">
        <input type="text" placeholder="Name" value={props.name}
          onChange={(e) => onChange(e, "name")} className="aboutInput" />
        <input type="text" placeholder="LastName" value={props.lastName}
          onChange={(e) => onChange(e, "lastName")} className="aboutInput" />
        <input type="text" placeholder="Email" value={props.email}
          onChange={(e) => onChange(e, "email")} className="aboutInput" />
      </div>
      <div className="skills">
        <div className="skillRatings">
          <p className="description">HTML Level</p>
          <div></div>
          <div style={{ width: props.htmlLevel * 80 / 100 + "%" }}></div>
          <input type="text" value={props.htmlLevel} onChange={(e) => onChange(e, "html")} className="skillInput" /> %
        </div>
        <div className="skillRatings">
          <p className="description">CSS Level</p>
          <div></div>
          <div style={{ width: props.cssLevel * 80 / 100 + "%" }}></div>
          <input type="text" value={props.cssLevel} onChange={(e) => onChange(e, "css")} className="skillInput" /> %
        </div>
        <div className="skillRatings">
          <p className="description">JS Level</p>
          <div></div>
          <div style={{ width: props.jsLevel * 80 / 100 + "%" }}></div>
          <input type="text" value={props.jsLevel} onChange={(e) => onChange(e, "js")} className="skillInput" /> %
        </div>
      </div>
      <button className="submit btn btn-outline-success">SEND</button>
    </form>
  )
}

const CV = (props) => {
  useEffect(() => {
    positionFooter();
  })

  return (
    <div className="cv">
      <div className="title">
        <h2>Specify your skills</h2>
      </div>
      <CVForm {...props} />
    </div>
  )
}

const CVContainer = (props) => {
  return (
    <CV {...props} />
  )
}

let mapStateToProps = (state) => ({
  name: state.CVPage.name,
  lastName: state.CVPage.lastName,
  email: state.CVPage.email,
  htmlLevel: state.CVPage.htmlLevel,
  cssLevel: state.CVPage.cssLevel,
  jsLevel: state.CVPage.jsLevel
})

let mapDispatchToProps = (dispatch) => ({
  setName: (name) => {
    dispatch(setNameActionCreator(name));
  },
  setLastName: (lastName) => {
    dispatch(setLastNameActionCreator(lastName));
  },
  setEmail: (email) => {
    dispatch(setEmailActionCreator(email));
  },
  setHTML: (htmlLevel) => {
    dispatch(setHTMLActionCreator(htmlLevel));
  },
  setCSS: (cssLevel) => {
    dispatch(setCSSActionCreator(cssLevel));
  },
  setJS: (jsLevel) => {
    dispatch(setJSActionCreator(jsLevel));
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(CVContainer);